import { Component, OnInit } from '@angular/core';
import { MovieService } from '../movie/movie.service';

@Component({
  selector: 'app-my-list',
  templateUrl: './my-list.component.html',
  styleUrls: ['./my-list.component.css'],
  providers: [MovieService]
})
export class MyListComponent implements OnInit {
	movieList: any[];
	//movieList_bind: any[] = [1] ;
	//movieList_wached : any[] = [];


  constructor(private movieService: MovieService) { }

  fillMovieList(){
  	 var self = this;

  	 this.movieList.forEach(function(item, index, mList){

  	 	this.movieService.getMovieInfos(item['movie_id']).subscribe(
  	 		data=>{
  	 			mList[index].poster_path = data['poster_path'];
  	 			mList[index].title = data['title'];
  	 		}
  	 	);
  	 	
  	 	
  	 }.bind(this));

  }

  ngOnInit() {
  	 this.movieService.getMovieWached(1).subscribe(
        data => { 
        	this.movieList = data['results']; 
        	this.fillMovieList();
      });

  	

  	 /*
  	 this.movieService.getMovieInfos(281338).subscribe(
  	 	data => {
  	 		console.log(data);
  	 	}
  	 );
  	 */
  }

}



