import { Component, OnInit } from '@angular/core';
import { MovieService } from '../movie/movie.service';

declare var jquery:any;
declare var $ :any;

export class Movie {
  title: string;
  image: string;
  year: string;
}

@Component({
  selector: 'app-movies',
  templateUrl: './movies.component_v2.html',
  styleUrls: [],
  providers:[MovieService],
})

export class MoviesComponent implements OnInit {

  movieList: any[];
  movieList_bind: any[] = [1] ;
  movieList_wached : any[] = [];


  inputSearch : string = "";


  constructor(private movieService: MovieService) {}


  fillMovieList(){
  	var moviesWached = this.movieList_wached;
  	//this.movieList_bind = null;

    this.movieList.forEach(function(item, index, mList){

      var movie = {};	
      movie = item;
      mList[index].watched = false;
      mList[index].watched_date = null;
      
      //---
      //finding on list loaded (each loop) if id is IN "moviesWached" array
      //--


      var indexWached = moviesWached.findIndex(x => x.movie_id == item.id);
      if(indexWached!= -1){
        //if yes then mark as wached
        mList[index].watched = true;
        //add date wached
        //console.log();
        mList[index].watched_date = moviesWached[indexWached].watched_date;
      }

    },this);
  }





  onSearchChange() {
    var searchValue = this.inputSearch;
    if(searchValue.length>3){
      $('.slogan').addClass('closed');
      $('.search-bar').addClass('topBar');

      this.movieService.getMovies(searchValue).subscribe(
        data => { this.movieList = data['results']; 
        this.fillMovieList();

      });


    }else{
      this.movieList = null;
       $('.slogan').removeClass('closed');
      $('.search-bar').removeClass('topBar');
    }

  }



  addMovieList(idMovie : string, score: string){
    var d = new Date();


    // IF idMovie IS NOT ON THE LIST OF WACHED
    //if(this.movieList_wached.findIndex(x => x.id==idMovie)== -1){  
      this.movieService.setMovieWached(idMovie,score).subscribe( data => { 
        //console.log(data['results']); 
        //this.fillMovieList();

      });

        this.movieList_wached.push({movie_id:idMovie, watched_date: d.toString()});
        this.onSearchChange();
    //}
    


  }

 

  ngOnInit(): void {

   //this.movieService.getMovies();
   // Make the HTTP request:

    this.movieService.getMovieWached(1).subscribe(
        data => { this.movieList_wached = data['results']; 
        //console.log(data);

      });
  
  
  }



}



